<template>
	<div id="goods-detail">
		<public-header title="商品详情"></public-header>
		<div class="content">
			<div class="goods-img">
				<img :src="goodsActive.thumb_" alt="" v-if="goodsActive.slides && !goodsActive.slides.length">
				<van-swipe :autoplay="3000" indicator-color="#ff2d4b" v-else>
					<van-swipe-item v-for="(item, index) in goodsActive.slides " :key="index"ss>
						<img :src="item" alt="">
					</van-swipe-item>
				</van-swipe>
			</div>
			<div class="goods-name">
				<span>{{goodsActive.title}}</span>
				<span class="icon icon-share" @click="onToggleSharePopupStatus"></span>
			</div>
			<div class="sell-info">
				<template v-if="store.gconfig.goods_sailed_status == 1">
					已售{{goodsActive.sailed}}&nbsp;&nbsp;
				</template>
				<template v-if="store.gconfig.goods_comment_good_status == 1">
					好评{{goodsActive.comment_good}}
				</template>
			</div>
			<div class="goods-num">
				<van-row>
					<van-col span="18" class="price flex">
						<div>
							{{Lang.dollarSign}}
							<span class="fee">{{goodsActive.price}}<span class="font-14 font-bold">/{{goodsActive.unitname}}</span></span>
						</div>
						<div class="old-price" v-if="goodsActive.old_price > 0">{{Lang.dollarSign}}{{goodsActive.old_price}}</div>
						<div class="svip-price-tips margin-10-l" v-if="goodsActive.svip_status == 1 && !goodsActive.discount_price">
							<div class="svip-price">
								<i class="icon icon-crownfill"></i>
								<span>{{goodsActive.discount}}折</span>
							</div>
							<div class="svip-activity">超级会员专享</div>
						</div>
						<div class="kabao-price-wrap" v-else-if="goodsActive.kabao_status == 1 && !goodsActive.discount_price">
							<span class="kabao-price">
								￥{{goodsActive.kabao_price}}
							</span>
							<span class="kabao-label">
								<i class="icon icon-vip"></i>
								<span>{{goodsActive.kabao_group_title}}</span>
							</span>
						</div>
						<template v-else>
							<div class="discount" v-if="goodsActive.discount > 0 && goodsActive.discount < 10">
								<span class="icon icon-tag font-12"></span>
								<span>{{goodsActive.discount}}折</span>
							</div>
						</template>
						<div v-if="goodsActive.unitnum > 1">
							<span class="goods-unitnum">{{goodsActive.unitnum_multi_cn}}购</span>
						</div>
					</van-col>
					<goods-handle
						:goods="goodsActive"
						:optionId="0"
						from="detail">
					</goods-handle>
				</van-row>
			</div>
			<div class="goods-content" v-if="goodsActive.content">
				{{goodsActive.content}}
			</div>
			<div class="goods-evaluate">商家</div>
			<div class="enter-store" @click="util.jsUrl('/pages/store/goods', {sid:store.id})">
				<van-row class="flex-lr" gutter="10">
					<van-col span="3">
						<div class="store-logo">
							<img :src="store.logo" alt="">
						</div>
					</van-col>
					<van-col span="16">
						<div class="store-info">
							<div class="store-title">{{store.title}}</div>
							<div class="store-content">{{store.content}}</div>
						</div>
					</van-col>
					<van-col class="flex-lr font-12" span="5">
						<span>进店逛逛</span>
						<i class="icon icon-right c-gray"></i>
					</van-col>
				</van-row>
			</div>
			<div class="goods-evaluate">商品评价</div>
			<div class="praise text-center">
				好评率
				<span class="rate">{{goodsActive.comment_good_percent}}</span>
				<span class="num">(共{{goodsActive.comment_total}}人评价)</span>
			</div>
			<div class="progress">
				<div class="progress-bar">
					<div class="progress-active" :style="{width: goodsActive.comment_good_percent}"></div>
				</div>
			</div>
			<template v-if="comment.total > 0">
				<div class="goods-evaluate">商品评论</div>
				<div class="goods-comment">
					<div class="search-wrap">
						<div class="search-item" @click="util.jsUrl('/core/pages/store/goodsComment', {goods_id: goodsActive.id, thumb_status: 1})" v-if="comment.total_has_thumb > 0">
							<span>有图</span>
							<span>{{comment.total_has_thumb}}</span>
						</div>
						<div class="search-item" @click="util.jsUrl('/core/pages/store/goodsComment', {goods_id: goodsActive.id, activity: 1})" v-if="comment.total_activity_good > 0">
							<span class="icon icon-appreciate_fill_light"></span>
							<span>{{comment.total_activity_good}}</span>
						</div>
						<div class="search-item" @click="util.jsUrl('/core/pages/store/goodsComment', {goods_id: goodsActive.id, activity: 2})" v-if="comment.total_activity_bad > 0">
							<span class="icon icon-appreciate_fill_light rotate"></span>
							<span>{{comment.total_activity_bad}}</span>
						</div>
					</div>
					<div class="comment-item" v-for="(item, index) in comment.data" :key="index">
						<div class="comment-inner border-1px-b">
							<div class="left">
								<div class="avatar ">
									<img :src="item.avatar" />
								</div>
							</div>
							<div class="right">
								<van-row>
									<van-col span="17">
										<div class="w-100 ellipsis margin-5-t">{{item.username}}</div>
										<div class="flex margin-5-t" v-if="item.activity > 0">
											<span class="icon icon-appreciate_fill_light c-danger" :class="{rotate: item.activity == 2}"></span>
											<span class="font-12 c-gray">{{item.activity == 1 ? '赞' : '踩'}}了该商品</span>
										</div>
									</van-col>
									<van-col span="7" class="text-right font-12 c-gray">
										{{item.addtime_cn}}
									</van-col>
								</van-row>
								<div class="note" v-if="item.note">{{item.note}}</div>
								<div class="thumbs" v-if="item.thumbs && item.thumbs.length > 0">
									<van-row>
										<van-col span="6" v-for="(thumbItem, thumbIndex) in item.thumbs" :key="thumbIndex">
											<div class="thumb-item" @click="util.jsPreviewImage(item.thumbs, thumbIndex)">
												<div class="img-wrap">
													<img :src="thumbItem" />
												</div>
											</div>
										</van-col>
									</van-row>
								</div>
							</div>
						</div>
					</div>
					<div class="comment-total" @click="util.jsUrl('/core/pages/store/goodsComment', {goods_id: goodsActive.id})">
						<span>{{comment.total}}条外卖评价</span>
						<span class="icon icon-right font-12 c-gray"></span>
					</div>
				</div>
			</template>
			<div class="goods-desc">商品描述</div>
			<div class="goods-desc-con" v-html="goodsActive.description">
			</div>
		</div>
		<!--进入店铺-->
		<router-link class="into-store" tag="div" :to="util.getUrl({path: '/pages/store/goods', query: {sid: store.id}})" v-if="0">
			进入店铺
		</router-link>
		<!-- 购物车部分 -->
		<store-cart
			:show="true"
			:store="store"
			:pindan_id="pindan_id"
			@selectPinadan="selectPinadan">
		</store-cart>
		<transition name="loading">
			<iloading v-if="preLoading"></iloading>
		</transition>
		<i-van-share-sheet
			:status="sharePopupStatus"
			title="立即分享给好友"
			:query="{type: 1, id: goodsActive.id}"
			@onTogglePopup="onToggleSharePopupStatus"
		>
		</i-van-share-sheet>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	import StoreCart from '@/components/storeCart'
	import GoodsHandle from '@/components/goodsHandle'
	import IVanShareSheet from '@/components/iVanShareSheet'
	export default {
		data() {
			return {
				preLoading: true,
				cart: {},
				store: {
					gconfig: {}
				},
				comment: {
					total: 0,
					total_has_thumb: 0,
					total_activity_good: 0,
					total_activity_bad: 0,
					data: []
				},
				goodsActive: {},
				sharePopupStatus: false
			}
		},
		components: {
			PublicHeader,
			StoreCart,
			GoodsHandle,
			IVanShareSheet
		},
		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart'
			]),
			onToggleSharePopupStatus() {
				var that = this;
				that.sharePopupStatus = !that.sharePopupStatus;
			},
			selectPinadan(pindan_id) {
				this.pindan_id = pindan_id;
			},
			onSelectShare(option) {
				alert(option.name)
			},
			onLoad() {
				if(!this.$route.query.sid) {
					this.$toast('参数错误');
					return false;
				} else {
					this.sid = this.$route.query.sid
				}
				if(!this.$route.query.id) {
					this.$toast('参数错误');
					return false;
				} else {
					this.id = this.$route.query.id;
				}
				this.util.request({
					url: 'wmall/store/goods/detail',
					data: {
						sid: this.sid,
						id: this.id
					}
				}).then((res) => {
					this.preLoading = false;
					let result = res.data.message;
					if(result.errno) {
						return;
					}
					result = result.message;
					var goodsActive = result.goodsDetail;

					this.goodsActive = goodsActive;
					this.cart = result.cart.message.cart;
					this.store = result.store;
					this.comment = this.util.extend(this.comment, result.comment);
					this.replaceStore(result.store);
					this.replaceCart(result.cart.message.cart);
				})
			},
		},

		computed: {
			...mapState([
				'istore', 'icart',
			])
		},
		created(){
			this.query = this.$route.query;
			if(this.query) {
				this.pindan_id = 0;
				if(this.query.pindan_id > 0) {
					this.pindan_id = this.query.pindan_id;
				}
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	.van-share-sheet{
		z-index: 10000000 !important;
	}
	#goods-detail .enter-store{
		margin: 15px 0;
		padding: 0 15px;
	}
	#goods-detail .enter-store .store-logo{
		position: relative;
		width: 100%;
		height: 0;
		padding-top: 100%;
		border-radius: 5px;
		overflow: hidden;
	}
	#goods-detail .enter-store .store-logo img{
		position: absolute;
		z-index: 10;
		top: 0;
		width: 100%;
		height: 100%;
	}
	#goods-detail .enter-store .store-title{
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
		color: #333;
	}
	#goods-detail .enter-store .store-content{
		font-size: 12px;
		margin-top: 5px;
	}
	#goods-detail .content{
		position: absolute;
		top: 46px;
		left: 0;
		right: 0;
		bottom: 50px;
		overflow-y: auto;
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
		color: #6d6d72;
		padding-bottom: 20px;
	}
	#goods-detail .content::-webkit-scrollbar{
		display: none;
	}
	#goods-detail .content .goods-img{
		margin: 15px;
	}
	#goods-detail .content .goods-img .van-swipe{
		width: 100%;
	}
	#goods-detail .content .goods-img img{
		max-width: 100%;
		max-height: 300px;
		display: block;
		margin: 0 auto;
	}
	#goods-detail .content .goods-name {
		padding: 10px 15px 0 15px;
		font-size: 18px;
		font-weight: bold!important;
		color: #333;
		line-height: 30px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	#goods-detail .content .goods-name .icon{
		font-size: 20px;
		font-weight: normal;
	}
	#goods-detail .content .sell-info{
		color: #999;
		font-size: 12px;
		padding: 0 15px;
		line-height: 20px;
	}
	#goods-detail .goods-num {
		padding: 15px 15px 0 15px;
		line-height: 25px;
		position: relative;
	}
	#goods-detail .goods-num .foodop{
		right: 15px;
		bottom: 0;
	}
	#goods-detail .goods-num .price{
		color: #ef1e15;
		font-size: 16px;
	}
	#goods-detail .goods-num .price .fee{
		font-size: 22px;
	}
	#goods-detail .goods-num .old-price{
		color: #999;
		font-size: 12px;
		text-decoration: line-through;
		margin-left: 5px;
	}
	#goods-detail .goods-num .discount{
		display: flex;
		align-items: center;
		margin-left: 5px;
		color: #ff2d4b;
		font-size: 12px;
	}
	#goods-detail .goods-content{
		padding: 15px 10px 0;
		font-size: 12px;
		color: #999;
		line-height: 1.5;
	}
	#goods-detail .goods-num .operate-num .icon-plus {
		background: linear-gradient(to right,#FF930F,#FF5E4E);
		font-size: 16px;
		color: #fff;
		display: inline-block;
		height: 28px;
		width: 28px;
		line-height: 28px;
		text-align: center;
		border-radius: 100%;
	}
	#goods-detail .goods-num .operate-num .icon-minus {
		background: #fff;
		font-size: 16px;
		color: #ff2d4b;
		display: inline-block;
		height: 28px;
		width: 28px;
		line-height: 28px;
		text-align: center;
		border: 1px solid #ff2d4b;
		border-radius: 100%;
	}
	#goods-detail .goods-num .goods-detail-plus.no-sail {
		font-size: 10px;
		color: #888;
	}
	#goods-detail .goods-num .select-spec {
		display: inline-block;
		height: 28px;
		line-height: 28px!important;
		font-size: 12px;
		color: #fff;
		background: linear-gradient(to right,#FF930F,#FF5E4E);
		border-radius: 28px;
		padding: 0 8px;
	}
	#goods-detail .content .goods-evaluate,#goods-detail .content .goods-desc{
		margin: 12px 0;
		position: relative;
		text-align: center;
		color: #333;
	}
	#goods-detail .content .goods-evaluate:before,#goods-detail .content .goods-desc:before {
		left: 0;
		top: 50%;
		margin-top: -1px;
	}
	#goods-detail .content .goods-evaluate:after,#goods-detail .content .goods-desc:after {
		right: 0;
	}
	#goods-detail .content .goods-evaluate:before,#goods-detail .content .goods-desc:before,#goods-detail .content .goods-evaluate:after,#goods-detail .content .goods-desc:after {
		content: "";
		width: 30%;
		height: 1px;
		border-top: 1px solid #ddd;
		position: absolute;
		top: 50%;
		margin-top: -1px;
	}
	#goods-detail .content .praise{
		line-height: 25px;
	}
	#goods-detail .content .praise .rate {
		color: #ef1e15;
	}
	#goods-detail .content .praise .num {
		font-size: 12px;
		color: #ccc;
		margin-left: 10px;
	}
	#goods-detail .content .progress{
		padding: 10px 15px;
	}
	#goods-detail .content .progress .progress-bar{
		height: 20px;
		border-radius: 20px;
		background: #E6E6E6;
		position: relative;
		overflow: hidden;
	}
	#goods-detail .content .progress .progress-bar .progress-active {
		height: 100%;
		background: #ff2d4b;
	}
	#goods-detail .content .goods-desc-con {
		width: 100%;
		padding: 0 15px;
		font-size: 14px;
		overflow-x: hidden;
		padding-top: 5px;
	}
	#goods-detail .content .goods-desc-con img,#goods-detail .content .goods-desc-con div, #goods-detail .content .goods-desc-con p{
		max-width: 100% !important;
	}
	#goods-detail .content .goods-desc-con img{
		vertical-align: bottom;
	}
	#goods-detail .content .tips{
		padding: 0 15px;
		line-height: 25px;
		font-size: 14px;
		margin-top: 10px;
	}
	/*进入店铺*/
	#goods-detail .into-store{
		position:fixed;
		right:10px;
		bottom:100px;
		width:50px;
		height:50px;
		border-radius:100px;
		background: rgba(0,0,0,0.4);
		color:#fff;
		font-size:13px;
		text-align:center;
		box-sizing:border-box;
		padding:10px;
		display:flex;
		align-items:center;
		line-height: 1.5;
	}
	#goods-detail .goods-num .goods-unitnum{
		margin-left: 5px;
		padding: 0px 5px;
		color: #ef1e15;
		font-size: 11px;
		border: 1px solid #ef1e15;
	}
	/*超级会员折扣*/
	#goods-detail .goods-num .svip-price-tips{
		display: flex;
		align-items: center;
		line-height: 1;
	}
	#goods-detail .goods-num .svip-price-tips .svip-price{
		display: flex;
		align-items: center;
		background-color: #302d22;
		padding: 1px 5px;
		border-top-left-radius: 2px;
		border-bottom-left-radius: 2px;
		color: #fff2bd;
		font-size: 11px;
		border: 1px solid #302d22;
		border-right: none;
	}
	#goods-detail .goods-num .svip-price-tips .svip-price span{
		padding-left: 3px;
	}
	#goods-detail .goods-num .svip-price-tips .svip-price .icon-crownfill{
		font-size: 11px;
	}
	#goods-detail .goods-num .svip-price-tips .svip-activity{
		background-color: #fff2bd;
		color: #1c1a13;
		border: 1px solid #aca275;
		border-left: none;
		border-top-right-radius: 2px;
		border-bottom-right-radius: 2px;
		font-size: 11px;
		padding: 1px 0;
	}
	/*门店会员价格*/
	#goods-detail .kabao-price-wrap{
		display: flex;
		align-items: center;
		height: 18px;
		margin-left: 5px;
		font-size: 14px;
