	}
	#goods-detail .kabao-price-wrap .kabao-price{
		font-weight: bold;
		font-size: 16px;
		color: #705A41;
	}
	#goods-detail .kabao-price-wrap .kabao-label{
		position: relative;
		display: flex;
		align-items: center;
		padding: 2px 5px;
		margin-left: 5px;
		border-radius: 0 3px 3px 3px;
		font-size: 12px;
		background-color: #705A41;
		color: #D6B9A3;
	}
	#goods-detail .kabao-price-wrap .kabao-label:before{
		content: '';
		position: absolute;
		top: 0px;
		left: -4px;
		width: 0;
		height: 0;
		border-width: 5px;
		border-style: solid;
		border-color: #705A41 transparent transparent transparent;
